import React, { FC } from 'react';
import { MediaListItem } from "../MediaListItem";
import { VideoType } from "../../types/MediaTypes";

type MediaListType = {
    data: VideoType[];
};

export const MediaList: FC<MediaListType> = ({ data }) => (
    <>
        {data.map((item) => (
            <MediaListItem data={item} key={item.filename + item.headers["content-length"]}/>
        ))}
    </>
)
