import React, { createContext, FC, ReactNode, useContext, useState } from 'react';

type ChromeStateValueType = {
    state: boolean,
    updateState: () => void;
}

const ChromeContext = createContext(undefined as unknown as ChromeStateValueType);

export function useChromeContext () {
    return useContext(ChromeContext);
}

type ChromeProviderType = {
    children: ReactNode;
}

export const ChromeProvider: FC<ChromeProviderType> = ({ children }) => {
    const [state, setState] = useState(false);

    const updateState = () => {
        setState(prevState => !prevState)
    }

    return (
        <ChromeContext.Provider value={{state, updateState}}>
            {children}
        </ChromeContext.Provider>
    );
}