import React, { FC, useRef } from 'react';
import classes from './styles/index.module.scss';
import { Col } from "antd";
import { MenuIcon } from "../../icons/MenuIcon";
import { CSSTransition } from "react-transition-group";
import { FlexBox } from "../FlexBox";
import { PaddingBox } from "../PaddingBox";
import { Button } from "../Button";
import { useChromeContext } from "../../providers/ChromeProvider/ChromeProvider";
import { useExtensionId } from "../../hooks/useExtensionId";
import { getMenuItems } from './utils'

type MenuType = {
    isMenuVisible: boolean;
    setIsMenuVisible: React.Dispatch<React.SetStateAction<boolean>>;
}

export const Menu: FC<MenuType> = ({ isMenuVisible, setIsMenuVisible }) => {
    const nodeRef = useRef(null);
    const { updateState } = useChromeContext();
    const { extensionId } = useExtensionId();

    const closeHandle = () => {
        setIsMenuVisible(false);
    }

    return (
        <CSSTransition
            in={isMenuVisible}
            timeout={300}
            nodeRef={nodeRef}
            mountOnEnter
            unmountOnExit
            classNames={{
                enterActive: classes.menuContainer_visible,
                enterDone: classes.menuContainer_visible,
                exitActive: classes.menuContainer_hidden,
                exitDone:  classes.menuContainer_hiddenDone
            }}
        >
            <div className={classes.menuContainer} ref={nodeRef}>
                <div className={classes.background} onClick={closeHandle}/>
                <PaddingBox className={classes.menu} tinyVR mediumHR render={({children, ...props}) => (
                    <FlexBox gap='medium' column {...props}>
                        {children}
                    </FlexBox>
                )}>
                    <Button type='link' block onClick={closeHandle}>
                        <FlexBox align='middle' gutter={24}>
                            <MenuIcon/>
                            <Col>Menu</Col>
                        </FlexBox>
                    </Button>
                    <FlexBox column gap='tiny'>
                        {getMenuItems(extensionId).map(({ icon, title, onClick, href }) => {
                            const Icon = icon;
                            const handleClick = title === "Clear" ? () => {
                                if (onClick){
                                    onClick();
                                    updateState();
                                }
                            } : undefined;

                            return (
                                <Button type='text' block key={title} onClick={handleClick} href={href} target='_blank'>
                                    <FlexBox align='middle' gutter={24}>
                                        <Icon/>
                                        <Col>{title}</Col>
                                    </FlexBox>
                                </Button>
                            )
                        })}
                    </FlexBox>
                </PaddingBox>
            </div>
        </CSSTransition>
    );
}