import { formatBadgeText } from "../../utils";
import { changeBadgeText } from "./changeBadgeText";
import { StorageKey } from "../../background/const";
import { SiteWithMediaType } from "../../types/MediaTypes";

export const tabCallback = (tabId: string) => {
    chrome.storage.local.get([tabId], res => {
        const videos: SiteWithMediaType[] = res[tabId] ?? [];
        const badgeText = videos.length ? formatBadgeText(videos.length) : '';
        changeBadgeText(badgeText);
    });
    chrome.storage.local.set({
        [StorageKey.CurrentTab]: tabId,
    });
};