import { StorageKey } from "../../../background/const";
import { tabCallback } from "../../../chrome/utils";
import { Clear } from "../../../icons/Clear";
import { Facebook } from "../../../icons/Facebook";
import { HelpIcon } from "../../../icons/HelpIcon";
import { Star } from "../../../icons/Star";
// @ts-ignore
import { menuURLs } from 'browserConst';


export const getMenuItems = (extensionId: string) => {
    return [
        {
            onClick: () => {
                chrome.storage.local.get([StorageKey.CurrentTab], (res) => {
                    const key = res[StorageKey.CurrentTab] ?? 0;
                    chrome.storage.local.remove([`${key}`]);
                    tabCallback(`${key}`);
                })
            },
            title: 'Clear',
            icon: Clear,
        },
        {
            href: `${menuURLs.facebook}${extensionId}`,
            title: 'Facebook',
            icon: Facebook,
        },
        {
            href: `${menuURLs.help}${extensionId}`,
            title: 'Help',
            icon: HelpIcon,
        },
        {
            href: `${menuURLs.rateUs}${extensionId}`,
            title: 'Rate us',
            icon: Star,
        }
    ]
}