import React, { FC } from 'react'
import { Button } from "../Button";
import { Pause } from "../../icons/Pause";
import { Play } from "../../icons/Play";

type MediaListItemExpandIconType = {
    isActive?: boolean;
}

export const MediaListItemExpandIcon: FC<MediaListItemExpandIconType> = ({ isActive }) => (
    <>
        {isActive ? (
            <Button type="link" htmlType='button' icon={<Pause/>} style={{ transform: 'translateY(5px)' }}/>
        ) : (
            <Button type="link" htmlType='button' icon={<Play/>} style={{ transform: 'translateY(5px)' }}/>
        )}
    </>
);