import React, { FC, useEffect, useState } from 'react';
import classes from './styles/index.module.scss';
import { FlexBox } from "../FlexBox";
import { Collapse } from "antd";
import { MediaList } from "../MediaList";
import { StorageKey } from "../../background/const";
import { SiteWithMediaType } from "../../types/MediaTypes";
import { ErrorMessage } from "../ErrorMessage";
import { useChromeContext } from "../../providers/ChromeProvider/ChromeProvider";

const { Panel } = Collapse;

export const MainContent: FC = () => {
    const [videos, setVideos] = useState<SiteWithMediaType[]>([]);
    const [error, setError] = useState("");
    const isEmpty = !videos.length;
    const { state } = useChromeContext();

    useEffect(() => {
        chrome.storage.local.get([StorageKey.CurrentTab], (res) => {
            const key = res[StorageKey.CurrentTab] ?? 0;
            chrome.storage.local.get([key], (res) => {
                const mediaArr: SiteWithMediaType[] = res[key] ?? [];
                setVideos(mediaArr);
            })
        })

        chrome.storage.local.get([StorageKey.Errors], (res) => {
            const errors = res[StorageKey.Errors] ?? {};
            chrome.storage.local.get([StorageKey.CurrentTab], (res) => {
                const key = res[StorageKey.CurrentTab] ?? 0;
                setError(errors[key] ?? "");
            })
        })
    }, [state])

    return (
        <div className={classes.mainContent}>
            {!error ? (
                <>
                    {isEmpty ? (
                        <FlexBox justify='center' align='middle' className={classes.mainContent}>
                            (empty)
                        </FlexBox>
                    ) : (
                        <Collapse accordion size="small">
                            {videos.map(({ header, list, id }) => (
                                <Panel key={id} header={header}>
                                    <MediaList data={list} key={id}/>
                                </Panel>
                            ))}
                        </Collapse>
                    )}
                </>
            ) : (
                <ErrorMessage error={error}/>
            )}
        </div>
    );
}