import React, { FC, useEffect, useRef, useState } from 'react';
import { Collapse } from "antd";
import { PaddingBox } from "../PaddingBox";
import { formatBytes, getFileExtension } from "../../utils";
import { PlayerReference, StateListener } from 'video-react';
import "/node_modules/video-react/dist/video-react.css";
import { MediaListItemExpandIcon } from "./MediaListItemExpandIcon";
import { MediaListItemPanelHeader } from "./MediaListItemPanelHeader";
import { VideoType } from "../../types/MediaTypes";

const { Panel } = Collapse;

type MediaListItemType = {
    data: VideoType;
}

export const MediaListItem: FC<MediaListItemType> = (
    { data: { filename: title, url, headers } }
) => {
    const size = formatBytes(headers["content-length"]);
    const format = getFileExtension(headers['content-type'])
    const videoRef = useRef<PlayerReference>(null);
    const [isError, setIsError] = useState(false);

    useEffect(() => {
        if (videoRef.current) {
            videoRef.current.subscribeToStateChange(handleStateChange);
        }
    }, [videoRef.current]);

    const handleStateChange: StateListener = (state) => {
        // @ts-ignore
        if(state.error){
            setIsError(true)
        }
    };

    const handleCollapseChange: ((key: (string | string[])) => void) = (titles) => {
        if(videoRef.current){
            if(titles.length === 0) {
                videoRef.current.pause();
            }
            else {
                videoRef.current.play();
            }
        }
    }

    return (
        <PaddingBox tinyBottom>
            <Collapse
                accordion
                expandIcon={({ isActive }) => {
                    return <MediaListItemExpandIcon isActive={isActive} />
                }}
                onChange={handleCollapseChange}
            >
                <Panel key={title} header={(
                    <MediaListItemPanelHeader
                        title={title}
                        url={url ?? ""}
                        size={size}
                        format={format}
                    />
                )}>
                    {isError ? (
                        <>
                            Something went wrong. Try to watch this video in <a href={url} target='_blank' rel='noreferrer'>NEW TAB</a>
                        </>
                    ) : (
                        <video
                            style={{width: '100%'}}
                            crossOrigin={"anonymous"}
                            src={url}
                            autoPlay
                            controls
                            ref={videoRef}
                        />
                    )}
                </Panel>
            </Collapse>
        </PaddingBox>
    );
}