import React, { FC } from 'react'
import { FlexBox } from "../FlexBox";
import { Button } from "../Button";
import { Download } from "../../icons/Download";
import classes from "./styles/index.module.scss";

type MediaListItemPanelHeaderType = {
    url: string;
    title: string;
    size: string;
    format: string;
}

export const MediaListItemPanelHeader: FC<MediaListItemPanelHeaderType> = (
    { url, title, size, format }
) => {

    const handleDownloadClick: (React.MouseEventHandler<HTMLAnchorElement> & React.MouseEventHandler<HTMLButtonElement>) = (event) => {
        event.stopPropagation();
        chrome.downloads.download({
            url: url ?? '',
        });
    }

    return (
        <FlexBox gap='medium' justify='space-between' align='middle' >
            <Button  type="text" htmlType='button' icon={<Download/>} onClick={handleDownloadClick}/>
            <div className={classes.title} title={title}>{title}</div>
            <div>{format}</div>
            <div>{size}</div>
        </FlexBox>
    );
};